.events {
  margin: 0;
  padding: 0;
  list-style: none;
}
.events .ant-badge-status {
  width: 100%;
  overflow: hidden;
  font-size: 12px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.notes-month {
  font-size: 28px;
  text-align: center;
}
.notes-month section {
  font-size: 28px;
}

.ant-popover {
  z-index: 2;
}

.item-calendar {
  background-color: #f4f9fd;
  border-color: #c6daf0 !important;
}

.item-calendar:hover {
  background-color: #c6daf0;
}

.ant-modal-body {
  padding-top: 0;
}

.ant-list-item .events-options {
  display: none;
}

.ant-list-item:hover .events-options {
  display: block;
}

.ant-list-item {
  padding-left: 10px;
  padding-right: 15px;
}

.ant-list-item {
  box-shadow: 0 2px 2px 0 transparent;
}

.ant-list-item:hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
}

.ant-btn-dashed {
  border-color: dodgerblue;
}

.list-item-hour,
.list-item-time-diff {
  font-size: 0.75rem;
  margin-bottom: 5px;
}
